// import fetch from 'node-fetch'
import fetch from '@/utils/my-fetch';
import { API_HOST } from '@/config/constants';

// Part 1. ACTION TYPES
export const actionTypes = {
  GET_POST_LIST: 'GET_POST_LIST',
  GET_POST: 'GET_POST_LIST'
}

// Part 2 .ACTIONS
export const getPostList = ({ page = 1, limit = 5 }) => async dispatch => {
  const url = `${API_HOST}/api/articles?page=${page}&limit=${limit}&sort=-createdAt&status=published`;
  const result = await fetch(url);

  return dispatch({
    type: actionTypes.GET_POST_LIST, 
    data: result.code === 200 ? result.data : {} 
  })
}

export const getPost = ({ id }) => async dispatch => {
  const url = `${API_HOST}/api/articles/${id}?status=published`
  const result = await fetch(url);

  return dispatch({
    type: actionTypes.GET_POST, 
    data: result.code === 200 ? result.data : {
      article: { title: 'POST NOT FOUND' },
      previous: null,
      next: null
    }
  })
}
